import {Component, EventEmitter, Inject, Input, OnChanges, OnInit, Output} from '@angular/core';
import {DefaultAssets, KonversoInterface, OpenChatBotResponse, UserInput} from '../../interface/KonversoInterface';
import {KonversoService} from '../konverso.service';
import {TranslateService} from '../translate.service';
//import * as run from 'projects/konverso/assets/animatedback.js';
//import {KawaseBlurFilter} from "../../filters/kawase-blur/src/KawaseBlurFilter";
declare var PIXI: any;
const defaultInputType = 'text';
const defaultInputLimit = 300;

@Component({
    selector: 'bot-full-screen',
    templateUrl: './desktop-full-screen.component.html',
    styleUrls: ['./desktop-full-screen.component.css']
})
export class DesktopFullScreenComponent implements OnChanges, OnInit {
    @Input() AssistantMode: boolean = false;
    @Input() assets: DefaultAssets;
    @Input() firstVisit: boolean = false;
    @Input() firstUsageStory: string[];
    @Input() displayData: (UserInput | OpenChatBotResponse)[];
    @Input() disableUserInput: boolean;
    @Input() LastUserInput: UserInput;
    @Input() LastBotAnswer: OpenChatBotResponse;
    @Input() PlaceHolder: string[];
    @Input() IsMobile: boolean = false;
    @Input() showInput: boolean;
    @Input() NumberPlaceHolder: string[];
    @Input() afterProcess = false;

    @Output() readySend: EventEmitter<boolean> = new EventEmitter<boolean>(false);
    @Output() send: EventEmitter<UserInput> = new EventEmitter<UserInput>(null);
    @Output() sendBotCommand: EventEmitter<string> = new EventEmitter<string>(null);
    @Output() sendEvent: EventEmitter<string> = new EventEmitter<string>(null);
    public userInput: string;
    public currentPlaceHolder: string = '';
    public sendBtn = '';
    public select = '';
    public changed = false;
    public botListening = false;
    public showWrapper = false;
    public showText = false;
    public inputType: string = defaultInputType;
    public inputLimit: number = defaultInputLimit;
    private newMessage = false;
    private messageCurrent = '';
    private msgArray = [];
    private botListeningTimer = 0;
    private anim_done = false;
    private reloaded = false;
    private timer;
    config: KonversoInterface;
    private days = [];

    constructor(public translate: TranslateService, public service: KonversoService,
        @Inject('__NgxKonverso__') config: KonversoInterface) {
        service.lang.subscribe((r) => {
            if (service.locale) {
                this.sendBtn = translate.translate(service.locale, 'SEND');
                this.select = translate.translate(service.locale, 'SELECT');

                this.days = [
                    'MONDAY',
                    'TUESDAY',
                    'WEDNESDAY',
                    'THURSDAY',
                    'FRIDAY',
                    'SATURDAY',
                    'SUNDAY',
                    'MONDAY',
                ];
            }
        });

        this.config = config;
    }

    ngOnChanges() {
        console.log(this.config.AfterProcessScenario.FormButtons);
        console.log(this.service.locale);

        if (this.afterProcess) {
            this.LastUserInput = null;
            this.LastBotAnswer.text = this.config.AfterProcessScenario.BotMessage[this.service.locale];

            if (this.config.AfterProcessScenario.FormElements[0][0].label == 'Jour 1') {
                let day1 = this.days[new Date().getDay() - 1];
                let day2 = this.days[new Date().getDay()];
                let day3 = this.days[new Date().getDay() + 1];
                this.config.AfterProcessScenario.FormElements[0][0].label = day1;
                this.config.AfterProcessScenario.FormElements[1][0].label = day2;
                this.config.AfterProcessScenario.FormElements[2][0].label = day3;
            }
        }

        let t = setInterval(() => {
            if (document.querySelectorAll('.bot-answer')) {
                let elems = document.querySelectorAll('.bot-answer');
                if (elems.length > 0) {
                    let index = 0, length = elems.length;
                    let rep = true;
                    for (; index < length; index++) {
                        let temp = elems[index] as HTMLElement;
                        if (temp.style.opacity == '0') {
                            temp.style.opacity = '1';
                        }
                    }
                    this.anim_done = rep;
                    if (this.anim_done) {
                        clearInterval(t);
                    }
                }
            }
        }, 100);

        this.changed = false;

        if (document.getElementById('text') && !this.LastBotAnswer?.text.includes('loading-dots')) {
            document.getElementById('text').innerHTML = '';
        }

        console.log('-------> enter', this.LastBotAnswer?.text.includes('<number>') && this.LastBotAnswer?.text.includes('</number>'));
        if (this.LastBotAnswer?.text.includes('<number>') && this.LastBotAnswer?.text.includes('</number>')) {
            this.inputType = 'number';
            this.inputLimit = 999;
            this.currentPlaceHolder = this.NumberPlaceHolder[Math.floor(Math.random() * this.NumberPlaceHolder.length)];
        } else {
            this.currentPlaceHolder = this.PlaceHolder[Math.floor(Math.random() * this.PlaceHolder.length)];
            this.inputType = defaultInputType;
            this.inputLimit = defaultInputLimit;
        }

        console.log('TYPE %s MAX %d', this.inputType,  this.inputLimit, this.currentPlaceHolder)
        //console.log(this.LastBotAnswer);

        if (!this.anim_done) {
            let t2 = setInterval(() => {
                if (this.LastBotAnswer && !this.LastBotAnswer?.text.includes('loading-dots') && this.anim_done) {
                    clearInterval(t2);
                    const string = this.LastBotAnswer?.text
                        .split('<br/>').join(` `)
                        .split('&eacute;').join('é')
                        .split('&egrave;').join('è')
                        .replace(/<[^>]*>?/gm, '')
                        .split('&nbsp;').join('');

                    this.msgArray = string.split('');
                    if (this.messageCurrent !== string) {
                        this.newMessage = true;
                        this.messageCurrent = string;
                        this.launchLoop();
                    }
                    //this.looper(array, timer);
                }
            }, 100);
        } else {
            const string = this.LastBotAnswer?.text
                .split('<br/>').join(` `)
                .split('&eacute;').join('é')
                .split('&egrave;').join('è')
                .replace(/<[^>]*>?/gm, '')
                .split('&nbsp;').join('');

            this.msgArray = string.split('');
            if (this.messageCurrent !== string && string !== '') {
                this.newMessage = true;
                this.messageCurrent = string;
                this.launchLoop();
            }
        }

        setTimeout(() => {
            this.changed = true;
        }, 100);
    }

    launchLoop() {
        this.timer = setInterval(() => {
            if (this.msgArray.length == 0) {
                clearInterval(this.timer);
            }
            if (this.newMessage) {
                if (document.getElementById('text')) {
                    document.getElementById('text').innerHTML = '';
                }

                this.newMessage = false;
                //this.msgArray = this.messageCurrent.split("");
                clearInterval(this.timer);
                this.launchLoop();
            }
            this.looper();
        }, 60);
    }

    looper() {
        if (this.msgArray.length > 0 && !this.reloaded) {
            if (document.getElementById('text')) {
                document.getElementById('text').innerHTML += this.msgArray.shift();
            }
        }/*else {
        clearTimeout(timer);
      }*/
        /*timer = setTimeout(() => {
          this.looper(array, timer);
        }, 30);*/
    }

    ngOnInit(): void {
        this.inputType = defaultInputType;
        this.inputLimit = defaultInputLimit;
        if (this.PlaceHolder) {
            this.currentPlaceHolder = this.PlaceHolder[Math.floor(Math.random() * this.PlaceHolder.length)];
            setInterval(() => {
                this.currentPlaceHolder = this.PlaceHolder[Math.floor(Math.random() * this.PlaceHolder.length)];
            }, 3000);
        }

        setTimeout(() => {
            this.showWrapper = true;
        }, 2000);

        setTimeout(() => {
            this.showText = true;
        }, 2500);

        let t = setInterval(() => {
            if (document.querySelectorAll('.bot-answer')) {
                let elems = document.querySelectorAll('.bot-answer');
                if (elems.length > 0) {
                    let index = 0, length = elems.length;
                    let rep = true;
                    for (; index < length; index++) {
                        let temp = elems[index] as HTMLElement;
                        if (temp.style.opacity == '0') {
                            rep = false;
                        }
                    }
                    this.anim_done = rep;
                    if (this.anim_done) {
                        clearInterval(t);
                    }
                }
            }
        }, 100);

        //run.run();

        setInterval(() => {
            if (this.botListeningTimer > 0) {
                this.botListeningTimer -= 1;

                if (this.botListeningTimer > 0) {
                    document.getElementById('bot').className = 'a-cue-voice speaking';
                    document.getElementById('bot-icon').className = 'a-cue-icon speakingicon';
                } else {
                    document.getElementById('bot').className = 'a-cue-voice';
                    document.getElementById('bot-icon').className = 'a-cue-icon';
                }

                this.botListening = this.botListeningTimer > 0;
            }
        }, 500);
    }

    userWriting(key: any) {
        if (key.code === 'Enter') {
            this.botListening = false;
            this.botListeningTimer = 0;
        } else if (key.code === 'Backspace') {

        } else {
            this.botListening = true;
            if (this.botListeningTimer == 0) {
                this.botListeningTimer += 2;
            } else if (this.botListeningTimer < 5) {
                this.botListeningTimer += 1;
            }
        }
    }

    public emit($event) {
        this.firstVisit = false;
        this.readySend.emit(true);
    }

    public _send() {
        if (this.LastBotAnswer?.endOfTopic && this.LastUserInput) {
            this.LastUserInput.message = '';
        }

        this.botListening = false;
        const userData: UserInput = {
            message: this.userInput,
            date: new Date().toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        };
        this.send.emit(userData);
        this.userInput = null;
    }

    async scroll(scrollHeight: number) {
        return new Promise<number>((resolve) => {
            setTimeout(() => {
                resolve(0);
            }, 300);
        });

    }

    byPassUserInput(botdata: string, i?: number) {
        /*const buttons: NodeListOf<HTMLElement> = document.querySelectorAll('.show-btn');
        for (let btn of Array.from(buttons)) {
          btn.classList.add('hidden-btn');
        }*/
        const buttons: NodeListOf<HTMLElement> = document.querySelectorAll('.bot-answer');
        for (let btn of Array.from(buttons)) {
            btn.classList.add('hidden-btn');
        }
        this.sendBotCommand.emit(botdata);
        setTimeout(() => {
            const buttons: NodeListOf<HTMLElement> = document.querySelectorAll('.bot-answer');
            for (let btn of Array.from(buttons)) {
                btn.classList.remove('hidden-btn');
            }
        }, 1000);
    }
}
