import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TranslateService {
  private lang = {
      'fr': {
          'GO': `C'est parti`,
          'SEND': 'Envoyer',
          'SELECT': 'Vous devez sélectionner une réponse',
          'MONDAY': 'Lundi',
          "TUESDAY": 'Mardi',
          "WEDNESDAY": 'Mercredi',
          "THURSDAY": 'Jeudi',
          "FRIDAY": 'Vendredi',
          "SATURDAY": 'Samedi',
          "SUNDAY": 'Dimanche',
          "OTHER": "Autre",
          "FREE_FIELD": 'Champ libre',
          "VALIDATE": 'Valider',
          "SKIP": 'Passer',
      },
      'en': {
          'GO': `Let's go`,
          'SEND': 'Send',
          'SELECT': 'You must select an answer',
          'MONDAY': 'Monday',
          "TUESDAY": 'Tuesday',
          "WEDNESDAY": 'Wednesday',
          "THURSDAY": 'Thursday',
          "FRIDAY": 'Friday',
          "SATURDAY": 'Saturday',
          "SUNDAY": 'Sunday',
          "OTHER": 'Other',
          "FREE_FIELD": 'Free field',
          "VALIDATE": 'Validate',
          "SKIP": 'Skip',
      }
  }

  constructor() { }

  public translate(l, word) {
    return this.lang[l][word];
  }
}
